.header {
    background-color: $pink-icecream-bg;
    color: $white;
    width: 100%;
    position: relative;
    overflow: hidden;
    
    @media screen and (max-width: 767px){
        height: 502px;
    }

    @media screen and (min-width: 768px){
        height: 399px;
    }
    @media screen and (min-width: 1280px){
        height: 665px;
    }


    &__container {

        margin-left: auto;
        margin-right: auto;
        z-index: 3;

        @media screen and (max-width: 767px){
            max-width: 100%;
            padding: 6px 20px 0 20px;
            @include centeredFlex(space-between, center);
        }

        @media screen and (min-width: 768px){
            max-width: 700px;
            padding: 31px 0 0;
            @include centeredFlex(space-between, center);
        }

        @media screen and (min-width: 1280px){
            max-width: 1120px;
            padding: 37px 0 0;
            @include centeredFlex(space-between, center);
    }

    }

    &__buttons {
        @include centeredFlex(center, center);
    }

    &__button-menu {
        border: none;
        background-color: transparent;
        z-index: 2;

        @media screen and (min-width:768px) {
            margin-right: 30px;
        }

        @media screen and (min-width:1280px) {
            display: none;
        }
    }

    &__button-buy {

        @media screen and (max-width:767px) {
            display: none;
        }

        @include button ($white, $accent-color, 16px, 21px);
        width: 160px;
        height: 44px;
        font-weight: bold;

        &:hover, 
        &:focus {
            background-color: $button-hover;
            cursor: pointer;
        }
    }

    &__icon-icon-right {
        
        fill: $accent-color;
        border: 2px solid #D41443;
    }
}

.navigation {
    display: none;

    @media screen and (min-width: 1280px){
        @include centeredFlex(center, center);
        font-weight: bold;
        font-size: 16px;
        line-height: 1.312;
        z-index: 2;

        &__list {
            @include centeredFlex(center, center);
            
        }
    
        &__item {
            &:not(:last-child) {
                margin-right: 35px;
            }
        }
    
        &__link {
            color: $white;
    
            &--active, 
            &:hover, 
            &:focus {
                color: $accent-color;
            }
        }
    }
    

}

.hero {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    z-index: 1;

    &__container {
        position: relative;

        @media screen and (max-width: 767px){
            max-width: 100%;
            height: 502px;
            margin-left: auto;
            margin-right: auto;
        }

        @media screen and (min-width: 768px){
            max-width: 768px;
            height: 399px;
            margin-left: auto;
            margin-right: auto;
        }
        @media screen and (min-width: 1280px){
            max-width: 1280px;
            height: 665px;
            margin-left: auto;
            margin-right: auto;
        }
    }
}


.hero-title {

    @media screen and (max-width:767px) {
        width: 218px;
        padding-top: 193px;
        margin-left: 20px;
        z-index: 3;
    }

    @media screen and (min-width: 768px) {
        width: 168px;
        padding-top: 102px;
        margin-left: 34px;
    }

    @media screen and (min-width: 1280px) {
        width: 277px;
        padding-top: 171px;
        margin-left: 115px;
    }

    &__text{
        font-family: Titan One;
        font-weight: normal;
        letter-spacing: 0.04em;
        text-transform: uppercase;

        @media screen and (max-width:767px) {

            margin-bottom: 20px;

            &--white {
                color: $white;
                font-size: 26px;
                line-height: 1.153;
            }
    
            &--rose {
                color: $accent-color;
                font-size: 48px;
                line-height: 0.9552;
            }
        }
        

        @media screen and (min-width: 768px) {

            margin-bottom: 20px;

            &--white {
                color: $white;
                font-size: 22px;
                line-height: 1.145;
            }
    
            &--rose {
                color: $accent-color;
                font-size: 34px;
                line-height: 0.905;
            }
            
        }

        @media screen and (min-width: 1280px) {

            margin-bottom: 25px;
            
            &--white {
                color: $white;
                font-size: 38px;
                line-height: 1.157;
            }
    
            &--rose {
                color: $accent-color;
                font-size: 58px;
                line-height: 0.913;
            }
        }



    }

    &__buttons-list {
        display: flex;
    }
    
    &__buttons-link {

        @include centeredFlex(center, center);
        
        background-color: $white;
        color: $accent-color;

        @media screen and (max-width: 1279px) {
            width: 83px;
            height: 24px;
            border-radius: 20px;
            
            font-size: 8px;
            line-height: 1.25;

            &--red {
                width: 63px;
                background-color: $accent-color;
                color: $white;
                margin-right: 8px;
            }
        }

        @media screen and (min-width: 1280px) {
            width: 138px;
            height: 40px;
            border-radius: 20px;
            
            font-size: 14px;
            line-height: 1.285;

            &--red {
                width: 102px;
                background-color: $accent-color;
                color: $white;
                margin-right: 15px;
            }
        }
        
    }
}

.hero-icecream {
    position: absolute;
    bottom: 0;

    @media screen and (max-width:767px) {
        left: 40%;
        z-index: -1;

        &__picture {
            &::before {
                content: "";
                top: -3.5%;
                right: -52%;
                width: 346px;
                height: 346px;
                position: absolute;
            
                
                border-radius: 50%;
                background-color: $pink-dark-bg;
                z-index: -1;
            }
        }
    }

    @media screen and (min-width:768px) {
        left: 378px;

        &__picture {
            &::before {
                content: "";
                top: -10%;
                right: -78%;
                width: 324px;
                height: 324px;
                position: absolute;
            
                
                border-radius: 50%;
                background-color: $pink-dark-bg;
                z-index: -1;
            }
    }
    }

    @media screen and (min-width:1280px) {
        left: 594px;

        &__picture {
            &::before {
                content: "";
                top: -10%;
                right: -78%;
                width: 538px;
                height: 538px;
                position: absolute;
            
                
                border-radius: 50%;
                background-color: $pink-dark-bg;
                z-index: -1;
            }
    }
    }
}

.hero-milk {

    display: none;
    position: absolute;

    @media screen and (min-width: 768px) {
        
        bottom: 28px;
        left: 34px;  
        display: flex;
    }

    @media screen and (min-width: 1280px) {
       
        bottom: 45px;
        left: 77px;  
        display: flex;
    }
    

    &__info {
        align-self: flex-end;
        padding-bottom: 10px;
    }
    
    &__text {

        @media screen and (min-width: 768px) {
            width: 114px;
            height: 48px;
            margin-bottom: 7px;
            font-size: 8px;
            line-height: 1.5;
            letter-spacing: 0;
        }
    
        @media screen and (min-width: 1280px) {
            width: 200px;
            height: 78px;
            margin-bottom: 20px;
            font-size: 14px;
        }
       
    }

    &__icon {

        @include centeredFlex(center, center);
        border-radius: 50%;
       
        background-color: $white;
        fill:$accent-color;
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

        @media screen and (min-width: 768px) {
            width: 18px;
            height: 18px;
        }

        @media screen and (min-width: 1280px) {
            width: 26px;
            height: 26px;
            
        }

    }

    &__icon-right {
        

        @media screen and (min-width: 768px) and (max-width:1279px) {
            
            &--desktop {
                display: none;
            }
        }

        @media screen and (min-width: 1280px) {
            &--tablet {
                display: none;
            }
            
        }
    }
}

.hero-girl {
    position: absolute;
    bottom: 0;

    @media screen and (max-width:767px) {
        display: none;
    }

    @media screen and (min-width:768px) {
        right: 34px;
    }

    @media screen and (min-width:1280px) {
        right: 109px;
    }
}

.hero-decorations {
    position: absolute; 
    
    @media screen and (max-width:767px) {
        display: none;
    }

    @media screen and (min-width:768px) {
       top: 94px;
       right: 34px;
    }
    @media screen and (min-width:1280px) {
       top: 160px;
       right: 115px;
    }

    &__item {

        &:not(:last-child) {

            @media screen and (min-width:768px) {
            margin-bottom: 13px;
            }

            @media screen and (min-width:1280px) {
            margin-bottom: 15px;
            }
        }

        &::before {

            @media screen and (min-width:768px) {
                content: "";
                right: -8px;
                width: 16px;
                height: 16px;
                position: absolute;
                border-radius: 50%;
                background-color: $white;
                z-index: -1;
            }
    
            @media screen and (min-width:1280px) {
                content: "";
                right: -16px;
                width: 30px;
                height: 30px;
                position: absolute;
                border-radius: 50%;
                background-color: $white;
                z-index: -1;
            }
            
        }
    }

    &__number {
        font-family: Titan One;
        font-style: normal;
        font-weight: normal;
        color: $accent-color;
        text-align: right;
        letter-spacing: 0.04em;

        @media screen and (min-width:768px) {
            font-size: 28px;
            line-height: 1.142;
        }

        @media screen and (min-width:1280px) {
            
            font-size: 46px;
            line-height: 1.152;
            
        }

    }

    &__text {
        font-family: DM Sans;
        font-style: normal;
        font-weight: normal;
        text-align: right;
        color: $white;


        @media screen and (min-width:768px) {
            font-size: 8px;
            line-height: 1.125;
        }

        @media screen and (min-width:1280px) {
            
            font-size: 14px;
            line-height: 1.285;

        }
    }
}

.mobile-menu {

    &.is-open {
        opacity: 0;
        pointer-events: none;
    }
    overflow: visible;
    position: fixed;
    background-color: #E17992;
    overflow: visible;
    top: 0;
    right: 0;
    z-index: 4;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    padding-top: 60px;
    
    @media screen and (max-width:767px) {
        width: 250px;
        height: 502px;
        filter: drop-shadow(-8px 0px 20px rgba(0, 0, 0, 0.25));
    }
    @media screen and (min-width:768px) {
        width: 254px;
        height: 100vh;
        filter: drop-shadow(-8px 0px 20px rgba(0, 0, 0, 0.25));
    }
    @media (min-width: 1280px) {
        display: none;
      }


    &__button-close {
        position: absolute;
        right: 34px;
        top: 23px;
        border: none;
        background-color: transparent;
        cursor: pointer;

        &:hover,
        &:focus {
            stroke: $accent-color;
        }

    }

    &__button-buy {
        @include button ($white, $accent-color, 16px, 21px);
        width: 186px;
        height: 44px;
        display: block;
        margin-right: auto;
        margin-left: auto;

        &:hover, 
        &:focus {
            background-color: $button-hover;
            cursor: pointer;
        }
    }

    &__navigation-list {
        position: absolute;
        top: 134px;
        width: 100%;
    }

    &__item {
        display: flex;
        align-items: center;
        height: 42px;
        padding-left: 24px;
        font-weight: 700;
        font-size: 14px;
        line-height: 1.285;
        border: 1px solid #e18298;
        
    }

    &__link {
        color: $white;

        &--active, 
        &:hover, 
        &:focus {
            color: $accent-color;
        }
    }

}

